@import url("https://fonts.googleapis.com/css2?family=Playfair+Display:ital,wght@0,400;0,500;0,600;0,700;0,800;0,900;1,400;1,500;1,600;1,700;1,800;1,900&family=Raleway:wght@100;200;300;400;500;600;700;800;900&display=swap");

body {
  font-family: "Playfair Display", serif;
}

@mixin fontRale($fs, $fw, $lh) {
  font-family: "Raleway", serif;
  font-size: $fs;
  font-weight: $fw;
  line-height: $lh;
}

@mixin fontFair($fs, $fw, $lh) {
  font-family: "Playfair Display", serif;
  font-size: $fs;
  font-weight: $fw;
  line-height: $lh;
}
